// project imports
import React, { useState } from 'react';
import MainCard from 'ui-component/cards/MainCard';
import SecondaryAction from 'ui-component/cards/CardSecondaryAction';
import { gridSpacing } from 'store/constant';
import { Grid, TextField, ListItemButton, ListItemIcon, ListItemText } from '@mui/material';
import MuiTypography from '@mui/material/Typography';
import { useSelector } from 'react-redux';
import SendIcon from '@mui/icons-material/Send';
// ==============================|| TYPOGRAPHY ||============================== //
const Contact = () => {
    const title = 'Contact';
    const text = "Leave a message, I'll get back to you as soon as possible!";
    const customization = useSelector((state) => state.customization);
    const [name, setName] = React.useState('');
    const [email, setEmail] = React.useState('');
    const [message, setMessage] = React.useState('');

    const handleSubmit = () => {
        if (!name || !email || !message) {
            alert('Please fill all fields');
            return;
        }
        window.open(`mailto:w0d@hotmail.com?subject=Message from ${name}&body=${message} \nReply to: ${email}`);
    };

    return (
        <MainCard title={title} secondary={<SecondaryAction link="https://github.com/peekok" />}>
            <Grid container spacing={gridSpacing}>
                <Grid item xs={12} sm={14}>
                    <MuiTypography textAlign={'center'} variant="h3">
                        {text}
                    </MuiTypography>
                    <TextField
                        style={{ margin: '30px', width: '100%', maxWidth: '-webkit-fill-available', height: 'auto' }}
                        placeholder="Name"
                        label="Name"
                        color="secondary"
                        variant="standard"
                        error={name.length < 4 && name.length > 0}
                        required
                        onChange={(e) => {
                            setName(e.target.value);
                        }}
                    />
                    <TextField
                        style={{ margin: '30px', width: '100%', maxWidth: '-webkit-fill-available', height: 'auto' }}
                        placeholder="Email"
                        label="Email"
                        color="secondary"
                        variant="standard"
                        error={!email.includes('@') && email.length > 0}
                        required
                        onChange={(e) => {
                            setEmail(e.target.value);
                        }}
                    />
                    <TextField
                        style={{ margin: '30px', width: '100%', maxWidth: '-webkit-fill-available', height: 'auto' }}
                        placeholder="Message"
                        multiline
                        color="secondary"
                        variant="standard"
                        error={message.length < 10 && message.length > 0}
                        label="Message"
                        required
                        onChange={(e) => {
                            setMessage(e.target.value);
                        }}
                    />
                    <Grid
                        style={{
                            display: 'block',
                            marginLeft: 'auto',
                            marginRight: 'auto',
                            width: '-webkit-fill-available',
                            maxWidth: 'fit-content'
                        }}
                    >
                        <ListItemButton
                            sx={{
                                borderRadius: `${customization.borderRadius}px`
                            }}
                            onClick={() => {
                                handleSubmit();
                            }}
                        >
                            <ListItemIcon>
                                <SendIcon stroke={1.5} size="1.3rem" />
                            </ListItemIcon>
                            <ListItemText primary={<MuiTypography variant="body2">Send</MuiTypography>} />
                        </ListItemButton>
                    </Grid>
                </Grid>
            </Grid>
        </MainCard>
    );
};
export default Contact;
